.button{
	padding: 10px;
	margin-right:6px;
	border-radius: 10px;
	background-color: rgba(255,255,255,0.6);
	color: rgb(23, 26, 45);
	-webkit-transition: all 500ms;
	-o-transition: all 500ms;
	transition: all 500ms;
}

.button:hover{
	background-color: rgba(255,255,255,1);
	cursor: pointer;
}

@media (max-width: 700px){
	.button{
		margin-top: 3px;
	}
}


