.title-text{
	font-family: 'PT Sans', sans-serif;
	font-weight: 900;
	font-size: 400%;
	color: rgb(255,255,255);
	-webkit-transition: all 2s;
	-o-transition: all 2s;
	transition: all 2s;
}

@media (max-width: 700px) {
	.title-text{
		font-size: 600%;
	}
}

@media (max-width: 515px) {
	.title-text{
		font-size: 300%;
	}
}


