.loading-page{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	bottom: 0px;
	right: 0px;
	top:0px;
	left: 0px;
	z-index: 100;
	position: fixed;
	background-color: rgba(255,255,255,0.9);
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}

.loaded{
	opacity: 0;
}

h1{
	font-size: 1500%;
	margin:10px;
	color: rgb(23, 26, 45);
}


